import PressAreaPopup from '../press-area/press-area-popup';
import { getHideBaseStyleMixin } from '../mixins/act/hide-base-style';
import { getActClass } from './utils';


export default {
  name: 'PressActInputAddressDialog',
  options: {
    styleIsolation: 'shared',
  },
  components: {
    PressAreaPopup,
  },
  mixins: [getHideBaseStyleMixin()],
  props: {
    show: {
      type: Boolean,
      default: false,
      required: false,
    },
    cancelText: {
      type: String,
      default: '取消',
      required: false,
    },
    confirmText: {
      type: String,
      default: '确定',
      required: false,
    },
    propsData: {
      type: Object,
      default: () => ({

      }),
    },
    gameName: {
      type: String,
      default: '',
    },
    areaList: {
      type: Object,
      default: () => ({
        province_list: {
          110000: '北京市',
          120000: '天津市',
        },
        city_list: {
          110100: '北京市',
          120100: '天津市',
        },
        county_list: {
          110101: '东城区',
          110102: '西城区',
          120101: '和平区',
          120102: '河东区',
        },
      }),
    },
    type: {
      type: String,
      default: '',
    },
    useTipClass: {
      type: Boolean,
      default: false,
    },
    hideTipStyle: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    'clickButton',
    'clickCloseButton',
    'update:propsData',
    'update:show',
  ],
  data() {
    return {
      mUseraddr: {},
      showAddressPopup: false,
      selectArea: '110101',
    };
  },
  created() {},
  mounted() {
    this.mUseraddr = { ...this.propsData };
    if (this.mUseraddr) {
      // 如果地址里面有带*号的，需要置空重新填写，否则更新失败
      Object.keys(this.mUseraddr).forEach((key) => {
        if (this.mUseraddr[key] && this.mUseraddr[key].indexOf('*') !== -1) {
          this.mUseraddr[key] = '';
        }
      });
      // 如果省份城市为空，则地区也清空（后台返回时把省份城市转成*号返回了）
      if (!this.mUseraddr.province || !this.mUseraddr.city) {
        this.mUseraddr.province = '';
        this.mUseraddr.city = '';
        this.mUseraddr.district = '';
      }
    }
  },
  methods: {
    closeDialog() {
      this.$emit('update:show', false);
      this.$emit('clickCloseButton');
    },
    onClickButton() {
      this.$emit('clickButton');
    },
    onConfirmArea(value) {
      this.showAddressPopup = false;
      this.mUseraddr = {
        ...this.mUseraddr,
        province: value[0].name,
        city: value[1].name,
        district: value[2].name,
      };
      this.changeAddress();
    },
    changeAddress() {
      this.$emit('update:propsData', { ...this.mUseraddr });
    },
    getActClass(...args) {
      return getActClass(this.useTipClass, args);
    },
  },
};
